import React, { Component } from 'react';

function ClickItem(props, state) {
    return (
        <div
            style={
                {
                    backgroundImage: `url(${props.src})`,
                    width: "100%"
                }
            }
            className="clickImage"
            alt={props.alt}
            id={props.id}
            clicked={props.clicked}
            onClick={() => props.handleClickChange(props.id)}
        />
    );
}

export default ClickItem;